.solution-problems {
  &__image {
    display: flex;
    align-items: center;
    justify-content: center;
    padding-top: 30px;
    padding-bottom: 30px;

    img {
      max-width: 524px;
      width: 100%;
    }
  }
  
  &__list {
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin-left: -60px;
    margin-bottom: 0;
    padding: 40px 70px 40px 90px;
    background:#FFFFFF;
    border-radius:90px 0px 90px 0px;
  }

  &__title {
    margin-bottom: 27px;
    font-size: 36px;
    font-weight: bold;
    color: #333333;
    line-height: 1;
  }

  &__item {
    display: flex;
    align-items: center;
    padding: 18px 0;

    &-index {
      display: flex;
      align-items: center;
      justify-content: center;
      width:28px;
      height:28px;
      background:#2661E9;
      border-radius:50%;
      color: #FFFFFF;
      font-size: 18px;
    }

    &-text {
      flex: 1;
      margin-left: 16px;
      margin-bottom: 0;
      font-size: 16px;
    }
  }
}

@media (max-width: 991px) {
  .solution-problems {
    &__list {
      margin-left: 0;
      margin-top: -90px;
    }
  }
}

@media (max-width: 767px) {
  .solution-problems {
    &__list {
      padding: 50px;
    }
  }
}
